@import "../../mixin_media";

footer {
  flex-flow: column;
  flex: 0 0 auto;
  background: repeat url(/pic/srvc/main/header-big.png);
  padding: 10px;
  min-height: 110px;
  color: #9b9b9b;
  font-size: 13px;

  a {
    color: #ffffff;
  }

  p {
    font-size: 13px;
  }

  .menu {
    display: flex;
    gap: 15px;
    justify-content: center;
    max-width: 700px;
    margin: 0 auto;
    border-bottom: 1px solid #7a7a7a;
    padding-top: 30px;
    padding-bottom: 30px;
    @include _600{
      gap: 0;
    }

    .column {
      a {
        width: 100px;
        padding: 5px;
        word-break: break-word;
      }
    }
 }
  .legal {
    display: flex;
    justify-content: space-between;
    gap: 100px;
  }

}







