@import "mixin_media";
@import "var";

@font-face {
  font-family: "RobotoMedium";
  font-style: normal;
  font-weight: 500;
  src: url("/public/src/font/RobotoMedium/RobotoMedium.woff") format("woff"),
  url("/public/src/font/RobotoMedium/RobotoMedium.ttf") format("truetype");
}

html {
  height: 100%;
  overflow-y: scroll;
}


body {
  position: relative;
  display: flex;
  min-height: 100%;
  flex-direction: column;
  margin: 0;
  font-family: arial, sans-serif;
  /*скрыть дерганье страницы в появлении полосы прокрутки*/
  padding: 0 calc(0px - (100vw - 100%)) 0 0;
  text-rendering: optimizeLegibility;
  overflow: hidden;
  background-color: $color-gray1;

  @include _500 {
    padding: 0;
  }

}


p {
  margin: 5px 0;
  font-size: 16px;
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

input,
textarea,
textarea:focus,
.button:focus {
  outline: transparent;
}

.df {
  display: flex !important;
}
[hidden] {
  display: none !important;
}

.none {
  display: none !important;
}

[contenteditable]:focus-visible{
  outline: transparent;
}