@import "../../mixin_media";

.testresult {
  display: flex;
  flex-flow: column;

  .head {
    display: flex;
    flex-flow: column;
    padding: 20px;
    p{
      margin: 5px 0;
      font-weight: 600;
      font-size: 1rem;
    }
    .to-list {
      @include colored_button();
      align-self: flex-start;
      padding: 10px;
      margin-bottom: 20px;
    }
  }
}

.test-results__table {
  display: grid;
  grid-template-columns: 130px auto 100px 120px 50px;
  font-size: 10px;
  margin-top: 20px;

  .item {
    padding: 7px 13px;
    border-bottom: 1px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .error {
    background-color: #ff29293b;
  }

  .suc {
    background-color: rgba(9, 255, 61, 0.23);
  }

  .del-btn {
    background-color: #fbaaaa;
    align-self: center;
    justify-self: center;
    cursor: pointer;
    border-radius: 3px;
    color: #ffffff;
  }

  .del {
    background-color: #ff5f5f;

    &:hover {
      background-color: #ff4d4d;
      font-weight: 600;
    }
  }


}




